<template>
    <div>
        <form @submit.prevent="entrada(negocio)" >
        <v-card
        :loading="l"
        :disabled="l"
         class="mx-auto" max-width="500">
    <v-img
    class="white--text align-end"
    gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
      src="@/assets/img/pexels-nikita-krasnov-9378368.jpg"
      height="250px" dark>

    <v-card-title class="display-1">
      Entrada
    </v-card-title>
</v-img>


<!--     <v-card-subtitle>
      1,000 miles of wonder
    </v-card-subtitle> -->

    <v-card-text>

      <v-text-field
      :loading="l"
      :disabled="l"
      label="Negócio"
      prepend-icon="mdi-store"  
      v-model="negocio.nome"
      required
    ></v-text-field>

    <v-text-field
    :loading="l"
    :disabled="l"
    type="password"
    label="Palavra-passe"
    v-model="negocio.senha"
    required
    :type="see ? 'text' : 'password'"
    @click:append="see = !see"
    :append-icon="see ? 'mdi-eye-off' : 'mdi-eye'"
    prepend-icon="mdi-lock"
    ></v-text-field>

    </v-card-text>
    
    <v-card-actions>
        <v-btn
        :loading="l"
        :disabled="l"
        type="submit"
        color="success"
        class="mr-4">
        Entrar
        <v-icon right> mdi-check </v-icon>
      </v-btn>
  
      <v-btn 
    :loading="l"
    :disabled="l"
      color="secondary" to="/novoNegocio" dark >
       Criar conta
      </v-btn>
    </v-card-actions>
  </v-card>
    </form>
    </div>
</template>

<script>
import service from '@/services/negocio'


export default {
    data() {
        return {
            see:false,
            l:true,
            negocio:{
                nome:"",
                senha:"",
            },
        }
    },

    methods: {
        async entrada(negocio){
            try {
                
                this.l = true;
 
  
               (await service.authNegocio(negocio));
 
               this.$root.toast.primary(`${negocio.nome} ok!`, 3000);
 
                this.l = false;
 
             } catch(error) {
 
                 this.l = false;
                 this.$root.toast.err(`${error}`, 3000);
                 // this.$root.toast.err(`${error}`, 3000);
 
 
             }  


        }
    },

    created() {
        setTimeout(() => {
            
            this.l = false
        }, 2000);
    },

}

</script>